.product-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: #e6e6e6;
    top: 0;
    left: 0;
  }

  &--all {
    display: flex;
    flex-direction: column;
  }
}

.product-card__title {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  margin: 0;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;

  &--all {
    display: block;
    text-align: center;
    margin-bottom: 8px;
  }
}

.product-card__details {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
}

.product-card__image {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
  align-self: center;
  justify-self: center;
}

.product-card__button {
  grid-column: -3 / -1;
  grid-row: 3 / 4;
  width: 100%;
  box-sizing: border-box;
  cursor: pointer;

  &--all {
    background-color: #f2f2f2;
    color: #444444;
  }

  &--all:hover {
    background-color: #ebebeb;
  }

  &--all:active {
    background-color: #ebebeb;
    color: rgba(68, 68, 68, 0.3);
  }
}

.product-card__description {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .font-arial();
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  margin: 0;

  &--all {
    display: block;
    text-align: center;
    margin-bottom: 21px;
  }
}

.product-card__definition {
  margin: 0;
}

.product-card__content--empty {
  padding-top: 58px;
  background-image: url("../img/plus.svg");
  background-repeat: no-repeat;
  background-position: center top;
}

@media (min-width: @tablet-width) {
  .product-card {
    background-color: #f2f2f2;
    padding: 0 36px 40px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .product-card::before {
    display: none;
  }

  .product-card__title {
    text-align: center;
  }

  .product-card__image {
    order: -1;
    margin-top: -73px;
    z-index: 1;
    margin-bottom: 34px;
  }

  .product-card__details {
    margin: 21px 0 42px;
    display: grid;
    gap: 16px;
  }

  .product-card__description {
    position: relative;
  }

  .product-card__description::before {
    content: "";
    position: absolute;
    background-color: #cdcdcd;
    height: 1px;
    width: 100%;
    top: 0;
    left: 0;
    transform: translateY(25px);
  }

  .product-card__button {
    font-size: 16px;
    line-height: 20px;
  }

  .product-card--all {
    background-color: @white;
    padding-top: 52px;
    border: 2px solid #f2f2f2;
  }

  .product-card__content--empty {
    padding-top: 161px;
    background-size: 60px;
  }

  .product-card__title--all {
    font-size: 20px;
    line-height: 22px;
    margin-bottom: 23px;
  }

  .product-card__description--all {
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 87px;
  }

  .product-card__description--all::before {
    display: none;
  }
}
